body {
  background-color: #88d3ff;
}

/* 0072F4; */
.App {
  background-color: #4da2f8;
  min-height: 500px;
  width: 500px;
  margin: 50px auto;
  border-radius: 25px;
  padding: 20px;
  padding-bottom: 35px;
}

.Title h1{
  color: #FFF;
  text-align: center;
  
}
/* 004AEE; */
#to-do-form input {
  background-color: #0074c5;
  border: 0;
  width: 350px;
  height: 50px;
  padding: 0 20px;
  border-radius: 10px;
  color: #fff;
  font-weight: bold;
  font-size: 16px;
}
#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-weight: bold;
  
}
#to-do-form input:focus,
#to-do-form button:focus {
  outline: none;
}

#to-do-form button {
  height: 50px;
  width: 95px;
  border-radius: 10px;
  border: 0;
  background-color: #0072f4;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  float: right;
}
