.list{
    color: #FFF;
    width: 400px;
    background-color: #004AEE;
    margin: 20px auto;
    border-radius: 10px;
    font-weight: bolder;
}

.list p{
    padding: 10px;
    position: relative;
}

.list p span{
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicons{
    margin: 0 5px;
    font-size: 15px;
}

.list p input{
    background-color: transparent;
    border: 0;
    color: #FFF;
    font-weight: bolder;
    font-size: 16px;
    resize: none;
    width: 350px;
    
}

.list p input:focus{
    outline: none;
}